@import url(https://fonts.googleapis.com/css?family=Montserrat);
* {
  font-family: 'Montserrat', 'sans-serif';
  color: white;
}

.navBackground {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  width: 300px;
  background: #313131;
}

nav {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 300px;
  z-index: 1000;
}

button {
  outline: none;
  border: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  cursor: pointer;
  position: absolute;
  top: 15px;
  left: 15px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: transparent;
}

ul,
li {
  margin: 0;
  padding: 0;
}

ul {
  padding: 25px;
  position: absolute;
  width: 230px;
}

li {
  list-style: none;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.icon-placeholder {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  flex: 40px 0;
  margin-right: 20px;
}

.text-placeholder {
  border-radius: 5px;
  width: 200px;
  height: 20px;
  flex: 1 1;
}

.refresh {
  padding: 10px;
  position: absolute;
  background: rgba(0, 0, 0, 0.4);
  border-radius: 10px;
  width: 20px;
  height: 20px;
  top: 10px;
  right: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

#root {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

body {
  margin: 0;
  font-family: 'Montserrat', 'sans-serif';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
}

code {
  font-family: 'Montserrat', 'sans-serif';
}

::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}

::-webkit-scrollbar-thumb {
  background-color: #424242;
}
.coin-icon {
    width: 25px;
}

.nav-container {
    position: relative;
    margin: auto;
}

.menu {
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 50%;
    background-color: #363636;
    padding: 12px;
    margin: 12px;
    cursor: pointer;
}

.nav-bar {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    width: 320px;
    background-color: #1a1a1a;
    z-index: 100;
}

.nav-head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-top: hidden;
    border: 2px;
    border-style: hidden hidden solid hidden;
    border-bottom-color: #363636;
}

.close-button {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 40px;
    width: 40px;
    margin-right: 10px;
    background-color: #202020;
    border-radius: 50%;
    cursor: pointer;
}

.nav-item {
    text-decoration: none;
    color: white;
    font-size: 20px;
}

.nav-item:hover {
    color: #a9a9a9;
}

.banner {
    height: 200px;
    width: 100%;
    margin-top: 50px;
    justify-content: space-around;
    background-color: #202020 !important;
}

.red {
    color: rgb(200, 0, 0);
}

.green {
    color: rgb(0, 170, 0);
}
